import React from 'react'

import Hero from '../../layout/Hero'
import Overview from '../../layout/Overview'
import Workspage from '../Workspage/Workspage'

import styled from 'styled-components'

const StyledHomepage = styled.div`
	display: flex;
	justify-content: space-between;
	flex-direction: column;

	& > div:first-of-type {
		// margin-bottom: 6rem;
	}

	@media only screen and (min-width: 835px) {
		flex-direction: row;
	}
`

class Homepage extends React.Component {
	constructor() {
		super()
		this.state = {
			homepage: true
		}
	}

	switchPageHandler = (e) => {
		window.scrollTo(0, 0);
		if (e.target.classList.contains('works_button')) {
			this.setState({
				homepage: !this.state.homepage
			})
		} else {
			this.setState({
				homepage: true
			})
		}
	}

	render() {
		return (
			<StyledHomepage>
				<Hero page={this.state.homepage} click={this.switchPageHandler} />
				{this.state.homepage ? <Overview click={this.switchPageHandler} page={this.state.homepage} /> : <Workspage click={this.switchPageHandler} page={this.state.homepage} />}
			</StyledHomepage >
		)
	}
}

export default Homepage
