import React from 'react'

import styled from 'styled-components'

import Work from './Work'
import ViewWorksBtn from '../../Components/ViewWorksBtn'
import info from '../../data/info.json'
import gsap from 'gsap'

const StyledWorkspage = styled.div`
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
	align-items: center;
	min-height: 130vh;
	padding-bottom: 5rem;
	margin-top: 4rem;
	
	@media only screen and (min-width: 835px) {
		padding-top: 8rem;
		width: 70%;
		margin-top: unset;
		min-height: unset;
	}

	@media only screen and (min-width: 1130px) {
		padding-top: 0;
		// height: 80vh;
		margin-top: 10vh;
	}
`

class Workspage extends React.Component {


	componentDidMount = () => {
		gsap.from('.staggerIn', { duration: 1, opacity: 0, y: -10, ease: 'ease', stagger: 0.1, delay: 0.3 })
	}

	render() {
		return (
			<StyledWorkspage>
				<Work entry={info.projects} />
				<ViewWorksBtn click={this.props.click} page={this.props.page} />
			</StyledWorkspage >
		)
	}
}

export default Workspage
