import React from 'react'

import styled from 'styled-components'

const StyledBtn = styled.button`
	text-transform: uppercase;
	padding: 2rem 10vw;
	border-radius: 15px;
	font-size: 3.5vw;
	letter-spacing: 2.5vw;
	outline: none;
	color: white;
	display: inline-block;
	text-decoration: none;
	font-weight: light;
	background: transparent;
	border: none;
	outline: none;
	box-shadow: -5px -5px 10px rgba(255,255,255, .25), 4px 7px 15px rgba(0,0,0, .25), 0px 4px 4px rgba(0,0,0, .25);
	@media only screen and (min-width: 500px) {
		letter-spacing: 10px;
		font-size: 1.8rem;
		padding: 2rem 6vw;
		font-weight: 100;
		&:hover{
			cursor: pointer;
		}
	}
`


function WorksBtn(props) {
	let buttonText = "";
	if (props.page) {
		buttonText = 'view my projects'
	} else {
		buttonText = 'go home'
	}
	return (
		<StyledBtn className="works_button staggerIn" onClick={props.click}>{buttonText}</StyledBtn>
	)
}

export default WorksBtn
