import React from 'react'
import styled from 'styled-components'

import AboutMe from '../Components/AboutMe'
import ViewWorksBtn from '../Components/ViewWorksBtn'
import Tech from '../Components/Tech'
import GenericOverviewModule from '../Components/GenericOverviewModule'

import info from '../data/info.json'

import gsap from 'gsap'

const StyledOverview = styled.div`
	padding: 0 3vw;
	padding-bottom: 8rem;
	display: flex;
	flex-direction: column;
	width: 100%;
	margin-top: 6rem;

	@media only screen and (min-width: 835px) {
		width: 50%;
		margin-top: unset;
	}
	@media only screen and (min-width: 900px) {
		padding: 0 6vw;
		padding-bottom: 8rem;
	}
	@media only screen and (min-width: 1000px) {
		width: 60%;
	}

	p, li{
		font-size: 1.6rem;
		font-weight: 300;
	}
	p, h3{
		letter-spacing: 0.2px;
		line-height: 1.6;
	}
	li{
		line-height: 1.3;
	}
	h4 {
		font-size: 1.6rem;
	}
	.overview{
		margin: 3rem 0;
		grid-template-columns: 1fr 4fr;
		@media only screen and (min-width: 480px) {
			display: grid;
		}
		@media only screen and (min-width: 835px) {
			display: initial;
		}
		@media only screen and (min-width: 1300px) {
			display: grid;
		}
		.section_title {
			color: #17BEBB;
			text-transform: uppercase;
			font-size: 1.6rem;
			width: 150px;
			line-height: 2;
		}
		.col_right{
			.section_entry {
				padding: 0.5rem 0;
				&:first-of-type{
					padding-top: 0;
				}
				&__heading{
					display: flex;
					justify-content: space-between;
					flex-direction: column;
					@media only screen and (min-width: 1260px) {
						align-items: center;
						flex-direction: row;
					}
					&--title{
						margin: 0.6rem 0;
						line-height: 1.4;
						.company{
							white-space: nowrap;
						}
						a{
							text-decoration: none;
							color: inherit;
							&:hover{
								text-decoration: underline;
							}
						}
					}


					&--date{
						font-size: 1.2rem;
					}
				}
				&__info {
					padding: 1rem 0;
				}
			}
		}
	}
	.tech{
		display: flex;
		flex-wrap: wrap;
	}
	.tech--hollow{
		color: #17BEBB;
		text-transform: lowercase;
		border: 1px solid #17BEBB;
		border-radius: 3px;
		display: inline;
		padding: 0.1rem 0.7rem;
		font-size: 1rem;
		font-weight: initial;
		margin: 1rem 1rem 0 0;
		font-family: 'Roboto Mono', monospace;
		@media only screen and (min-width: 1260px) {
			margin: 0 1rem 0 0;
		}
	}
	.light{
		font-weight: 100;
	}
	.works_button{
		margin: auto;
		display: inline-block;
		margin-top: 6rem;
	}
`

class Overview extends React.Component {

	componentDidMount = () => {
		gsap.from('.staggerIn', { duration: 1, opacity: 0, y: -10, ease: 'ease', stagger: 0.1, delay: 0.3 })
	}

	render() {
		return (
			<StyledOverview>
				<AboutMe aboutmeinfo={info.aboutme} />
				<Tech tech={info.techstack} />
				<GenericOverviewModule entry={info.experience} />
				<GenericOverviewModule entry={info.education} />
				<ViewWorksBtn click={this.props.click} page={this.props.page} />
			</StyledOverview>
		)
	}
}


export default Overview
