import React from 'react'

function GenericOverviewModule(props) {
	return (
		<div className="generic overview">
			<h3 className="section_title staggerIn">{props.entry.sectionTitle}</h3>

			<div className="col_right">
				{props.entry.details.map(e => (
					<div className="section_entry staggerIn" key={e.info.length > 0 ? e.info[0] : e.position}>
						<div className="section_entry__heading">
							<h4 className="section_entry__heading--title">
								<span className="position">{e.position}</span>
								{e.company && <span className="light"> @ </span>}
								{e.url ? <a href={e.url} target="_blank" rel="noopener noreferrer"><span className="company">{e.company}</span></a> : <span className="compant">{e.company}</span>}</h4>
							<p className="section_entry__heading--date">
								<span className="date--from">{e.date.from}</span>
								{(e.date.from !== "" && e.date.to !== "") && <span> - </span>}
								<span className="date--to">{e.date.to}</span>
							</p>
						</div>
						<ul className="tech">
							{e.tech.map(tech => <li className="tech--hollow" key={tech}>{tech}</li>)}
						</ul>
						{e.info.length > 0 && <div className="section_entry__info">{e.info.map(proposition => <p key={proposition}>{proposition}</p>)}</div>}
					</div>
				))}
			</div>
		</div>
	)
}

export default GenericOverviewModule
